html,
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*.game {
  margin: 10px;
}*/
@media (prefers-color-scheme: light) {
  .square {
    border: 1px solid #999;
    background: white;
  }
  .status {
    background: #f50057;
    color: white;
  }
}
@media (prefers-color-scheme: dark) {
  .square {
    border: 1px solid #ada6a6;
    background: #3c4a56;
    color: #ccbbbb;
  }
  .status {
    color: #f5f5f5;
    background: rgb(204, 0, 72);
  }
}

.status {
  width: 210px;
  margin-bottom: 10px;
  font-weight: bold;
}

.board {
  max-width: 231px;
  display: block;
}
.square {
  float: left;
  font-size: 33px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;

  height: 75px;
  width: 75px;

  display: flex;
  align-items: center;
  justify-content: center;
}

input {
  color: white;
}

ul {
  width: 350px;
}
li {
  list-style: none;
  height: 23px;
}

button {
  background: #61c561;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  overflow: hidden;
}

@media all {
  .board {
    max-width: 305px;
    max-height: 305px;
    display: flex;
  }
  .square {
    height: 100px;
    width: 100px;
  }
}

@media only screen and (min-width: 1024px) {
  .board {
    display: flex;
  }
  .square {
    height: 100px;
    width: 100px;
  }
}

