.BoardScreen {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.leftside {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.rightside {
  display: flex;
  flex-wrap: wrap;
  flex-basis: 25%;
  justify-content: center;
  padding: 0;
}

@media only screen and (min-width: 768px) {
  .BoardScreen {
    flex-direction: row;
    justify-content: center;
    max-width: 100vw;
  }
